import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    username: "",
    avator: ""
  },
  mutations: {
    updateUserName(state, newName) {
      state.username = newName;
    },
    updateAvator(state, newAvator) {
      state.avator = newAvator;
    }
  },
  actions: {
    updateUserName({ commit }, newName) {
      commit("updateUserName", newName);
    },
    updateAvator({ commit }, newAvator) {
      commit("updateAvator", newAvator);
    }
  }
});
