import axios from "axios";
import errorHandler from "@/utils/errorHandler";

import { Toast } from "vant";
axios.defaults.baseURL = process.env.NODE_ENV == "production" ? "" : "";
// Add a request interceptor
axios.interceptors.request.use(
  config => {
    if (/\/api\/*/.test(config.url)) {
      const token = sessionStorage.getItem("auth_token");
      config.headers.common["authorization"] = "hys " + token;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// Add a response interceptor
axios.interceptors.response.use(
  response => {
    // 处理没有权限的问题
    if (response.data.code === 401) {
      Toast(response.data.message);
      location.href = `${axios.defaults.baseURL}/login`;
    }
    // 针对错误码进行处理
    errorHandler(response.data.code, response.data.message);
    return response;
  },
  error => {
    // 针对http错误码进行处理
    errorHandler(error.response.status, error.response.data.message);

    return Promise.reject(error);
  }
);

export default axios;
