<template>
  <div class="home">
    <div class="main">
      <router-view />
    </div>
    <van-tabbar active-color="#24afdd" class="tabbar" v-model="active">
      <van-tabbar-item
        v-for="(tab, index) in tabbar"
        :icon="tab.icon"
        :to="tab.path"
        :dot="tab.dot"
        :info="tab.info"
        :key="index"
      >
        {{ tab.name }}
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import axios from "@/axios";
import { mapActions } from "vuex";
export default {
  name: "home",
  data() {
    return {
      active: -1,
      tabbar: [
        {
          name: "主页",
          path: "/",
          pathName: ["function", "todolist", "habitlist", "birthlist"],
          icon: "home-o",
          dot: false,
          info: ""
        },
        {
          name: "新建",
          path: "/new",
          pathName: ["new", "new_todo", "new_habit", "new_birth"],
          icon: "plus",
          dot: false,
          info: ""
        },
        {
          name: "我",
          path: "/me",
          pathName: ["me", "user_msg", "change_pwd", "fallback", "manual"],
          icon: "user-o",
          dot: false,
          info: ""
        }
      ]
    };
  },
  created() {
    const toName = this.$route.name;
    this.setActive(toName);

    axios.get("/api/get_user").then(res => {
      if (res.data.data.username != null) {
        this.updateUserName(res.data.data.username);
      } else {
        this.updateUserName("baymax");
      }

      if (res.data.data.avator != null) {
        this.updateAvator("/files/" + res.data.data.avator);
        let image = new Image();
        image.src = "/files/" + res.data.data.avator;
      } else {
        this.updateAvator("https://i.loli.net/2020/03/26/n1xvFwe4jkPECyL.png");
      }
    });
  },
  watch: {
    $route: "changeActive"
  },
  methods: {
    ...mapActions(["updateUserName", "updateAvator"]),
    changeActive({ name }) {
      this.setActive(name);
    },
    setActive(name) {
      this.tabbar.forEach((tab, i) => {
        tab.pathName.forEach(item => {
          if (item == name) {
            this.active = i;
            return false;
          }
        });
      });
    }
  }
};
</script>
<style scoped lang="stylus">
.tabbar
  box-shadow 0 0 10px #dadada
.main
  padding-bottom 100px
</style>
