import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./registerServiceWorker";

import {
  Cell,
  CellGroup,
  Button,
  Field,
  Tabbar,
  TabbarItem,
  Grid,
  GridItem,
  NavBar,
  Search,
  SwipeCell,
  Divider,
  DatetimePicker,
  Popup,
  DropdownMenu,
  DropdownItem,
  RadioGroup,
  Radio,
  Image,
  Uploader,
  Rate,
  Toast,
  Loading
} from "vant";

Vue.use(Tabbar).use(TabbarItem);
Vue.use(Cell)
  .use(CellGroup)
  .use(Button)
  .use(Field)
  .use(Grid)
  .use(GridItem)
  .use(NavBar)
  .use(Search)
  .use(SwipeCell)
  .use(Divider)
  .use(DatetimePicker)
  .use(Popup)
  .use(DropdownMenu)
  .use(DropdownItem)
  .use(RadioGroup)
  .use(Radio)
  .use(Image)
  .use(Uploader)
  .use(Rate)
  .use(Loading)

  .use(Toast);

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
